/* register.css */

/* Apply similar styling to the login page for consistency */
.body {
	font-family: Arial, sans-serif;
	background-color: #f4f4f4;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

.container {
	background-color: #fff;
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
	text-align: center;
	color: #333;
}

form {
	display: flex;
	flex-direction: column;
	max-width: 300px;
	margin: 0 auto;
}

label {
	margin-bottom: 8px;
}

input {
	padding: 8px;
	margin-bottom: 16px;
	border: 1px solid #ccc;
	border-radius: 4px;
}

button {
	background: red;
	color: #fff;
	padding: 10px;
	margin-top: 0;
	border: none;
	border-radius: 4px;
	cursor: pointer;
}

button:hover {
	background-color: gray;
}

.login-link {
	color: blue;
	text-decoration: none;
}

.login-link:hover {
	text-decoration: underline;
	color: red;
}
